@import url("https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap");

body,
html,
#root {
    height: 100%;
}

body {
    background-color: #222;
    margin: 0;

    &,
    button,
    textarea {
        color: #111;
        font-family: "PT Sans Narrow", Helvetica, Arial, sans-serif;
        font-size: 16px;
        font-weight: normal;
        line-height: 1.4rem;
    }

    button,
    textarea {
        appearance: none;
        border: 0;
        border-radius: 0;
    }

    textarea {
        background-color: #eee;
    }
}

#root {
    display: flex;
    flex-direction: column;
}

#custom button,
.next-button {
    height: 35px;
    width: 90px;
    font-weight: bold;
}

nav li,
#questions button,
.next-button,
#custom button {
    background-color: #999;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

    &:hover {
        background-color: #111;
        color: #eee;
        cursor: pointer;
    }
}

header {
    nav {
        height: 35px;

        ul {
            height: 100%;
            display: flex;
            list-style-type: none;
            gap: 5px;
            margin: 0;
            padding: 0;

            li {
                flex: 1;
                font-size: 15px;
                font-weight: bold;
                line-height: 1rem;
            }
        }
    }
}

main {
    display: flex;
    flex: 1;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;

    .slide {
        height: 100%;
        min-width: 100vw;
        box-sizing: border-box;
        color: #eee;
        overflow-y: auto;
        padding: 60px 10px;
        scroll-snap-align: start;

        &#home {
            #custom {
                display: flex;
                align-items: center;
                flex-direction: column;
                gap: 10px;

                textarea {
                    height: 80px;
                    width: 300px;
                }
            }

            #questions {
                display: flex;
                flex-direction: column;
                gap: 10px;

                &,
                button {
                    width: 100%;
                }

                button {
                    box-sizing: border-box;
                    flex-direction: column;
                    font-size: inherit;
                    font-style: italic;
                    padding: 10px;

                    h3 {
                        font-style: initial;
                        margin: 0 0 5px;
                        text-decoration: underline;
                    }
                }
            }
        }

        .slide-container {
            max-width: 800px;
            width: 100%;
            display: flex;
            align-items: center;
            flex-direction: column;
            gap: 20px;
            margin: auto;

            & > * {
                margin: 0;
                text-align: center;

                &:is(h1) {
                    margin-bottom: 25px;
                }
            }

            .card-image {
                height: 200px;
            }
        }
    }
}
